.slider__wrapper {
  position: relative;
  min-height: 120px;
}

.slider__list {
  padding: 0;
  margin: 0;
}

.slider__item {
  margin: 0;
  padding: 0;
  top: 0px;
  list-style: none;
  background-color: #ffffff;
  position: absolute;
  z-index: 0;
}

.slider__item--currant {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
  position: relative;
  z-index: 1;
}

